    /**
     * 
     * @param path 
     * @param options MaxWidthOrHeight 
     */
    compressImage(path: string, options: { targetWidth?: number, targetheight?: number, MaxWidthOrHeight?: number, quality?: number }): Observable<{ result: boolean, dataURL: string }> {
        return new Observable(observe => {
            let img = new Image(); 
            if (this.isIos()) {
                path = path.replace(/^file:\/\//, '');
            }
            img.src = path;
            img.onload = function () {
                // 
                let width = options.targetWidth || img.width;
                let height = options. targetheight ||img.height;
                if(!options.MaxWidthOrHeight)options.MaxWidthOrHeight = 1024;
                if(options.MaxWidthOrHeight < width || options.MaxWidthOrHeight < height){
                    let scale = 1;
                    if (width > height) { // > 
                        scale = width / options.MaxWidthOrHeight;
                    } else { // < 
                        scale = height / options.MaxWidthOrHeight;
                    }
                    width = width / scale;
                    height = height / scale;
                }
               
                let quality = options.quality || 1;  // 1
                //canvas
                let canvas = document.createElement('canvas');
                let ctx = canvas.getContext('2d');
                // 
                let anw = document.createAttribute("width");
                anw.nodeValue = width.toString();
                let anh = document.createAttribute("height");
                anh.nodeValue = height.toString();
                canvas.setAttributeNode(anw);
                canvas.setAttributeNode(anh);
                ctx.drawImage(img, 0, 0, width, height);
                //   
                let base64 = canvas.toDataURL('image/jpeg', quality);
                // base64
                observe.next({ result: true, dataURL: base64 });
            }
        })
    }

    /**
   * cordova-plugin-camera
   * @param options
   */
    getPicture(options: CameraOptions = {}): Observable<string> {
        let ops: CameraOptions = Object.assign({
            sourceType: this.camera.PictureSourceType.CAMERA,//,CAMERA:,PHOTOLIBRARY:
            destinationType: this.camera.DestinationType.DATA_URL,//base64,DATA_URL:base64   FILE_URI:
            quality: QUALITY_SIZE,//0 - 100
            allowEdit: true,//
            encodingType: this.camera.EncodingType.JPEG,
            saveToPhotoAlbum: true,//
            correctOrientation: true//
        }, options);
        return Observable.create(observer => {
            this.camera.getPicture(ops).then((imgData: string) => {
                if (ops.destinationType === this.camera.DestinationType.DATA_URL) {
                    observer.next('data:image/jpg;base64,' + imgData);
                } else {
                    observer.next(imgData);
                }
            }).catch(err => {
                if (err == 20) {
                    this.alert(',');
                    return;
                }
                if (String(err).indexOf('cancel') != -1) {
                    return;
                }
                this.logger.log(JSON.stringify(err), 'cordova-plugin-camera');
                //this.alert('');
                observer.error(false);
            });
        });
    };

