    saveBase64toFile(base64str: string): Observable<{ success: boolean, result: any, msg: string}>{ 
        return new Observable((observe)=>{
            let reg  = /data:image\/([^;]+).*/i 
            if(!reg.test(base64str)){ 
                observe.error({
                    success: false,
                    result: base64str,
                    msg: 'not image base64'
                })
                return ;
              }
            let mineType = (base64str).replace(reg,'$1');  
            let fileName = +new Date() + '.' + mineType; 
            let base64Data = base64str.replace(/^data:image\/\w+;base64,/, ""); 
            // let cacheDirctory = this.file.externalDataDirectory + '/';
            let cacheDirctory = this.file.dataDirectory + 'imageCache/';  
            let base64DataBuffer = Utility.base64ToArrayBuffer(base64Data)
            this.file.writeFile(cacheDirctory, fileName, base64DataBuffer).then(res=>{ 
                observe.next({
                    success: true,
                    result: res,
                    msg: 'ok'
                })
            }).catch(err=>{ 
                observe.error({
                    success: false,
                    result: err,
                    msg: 'saveBase64toFile failed'
                })
            })
        }) 
    }
  
    //
    //
    download(url, targetDirectory, fileName): Observable<any> {
        return Observable.create(observer => {
            let fileSize = 0;
            const FT: FileTransferObject = this.transfer.create();
            if (!url) observer.next(url);
            FT.onProgress(progressEvent => {
                if (progressEvent.lengthComputable) {
                    //  1
                    fileSize = progressEvent.total;
                    if (progressEvent.loaded / progressEvent.total == 1) {
                    }
                } else {

                }
            });

            FT.download(encodeURI(url), targetDirectory + fileName).then((entry) => {
                observer.next({ localUrl: entry.toURL(), fileSize: fileSize, fileName: fileName });
                // })
            }, (err) => {
                observer.next(err);
                this.logger.log(JSON.stringify(err), '')
            });
        })
    }

