import {Injectable} from '@angular/core';
import {Session} from "./session";
import * as fundebug from "fundebug-javascript";

/**
 * fundebug-javascript Javascript
 * @description
 */
@Injectable()
export class Logger {
  constructor(private session: Session) {
  }

  log(err: any, action: string, other = null): void {
    console.log(new Date() + `:Logger.log By ${this.session.username}:action- + ${action} =======> error: ${err}` );
    other && console.log(other);
    // console.log(new Date() + err);
    fundebug.notifyError(err,
      {
        metaData: {
          action: action,//
          other: other,//
          user: {id: this.session.userId, name: this.session.username}
        }
      });
  }

  httpLog(err: any, msg: string, other = {}): void {
    console.log('Logger.httpLogmsg-' + msg);
    fundebug.notifyHttpError(err,
      {
        metaData: {
          action: msg,//
          other: other,//
          user: {id: this.session.userId, name: this.session.username}
        }
      });
  }

}


